.main-header {
    width: 100vw;
    height: calc(100vh - 3rem);
    display: grid;
    place-items: center;
    margin-top: 3rem;
}

.main-header-containner {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 6rem;
}

.main-header-left {
    margin-top: -3rem;
}

.main-header-left h4 {
    margin-bottom: 1rem;
    color: var(--color-secondary);
    font-weight: 400;
}

.main-header-left p {
    margin: 1rem 0 3.5rem;
}

.main-header-right  {
    display: grid;
    place-items: center;
    position: relative;
}

.main-header-circle {
    width: 25rem;
    height: 25rem;
    background: linear-gradient(75deg, var(--color-secondary), transparent);
    border-radius: 50%;
    position: absolute;
    transition: var(--transition);
}

.main-header-image {
    position: relative;
}

.main-header-right:hover .main-header-circle {
    filter: blur(15px);

}